.header {
    padding: 32px 8px;
    position: absolute;
    z-index: 100;
    width: 100%;

    &__burger {
        display: none;
        @media screen and (max-width: 590px) {
            display: block;
        }
    }

    &__contacts {
        &-direct {
            display: flex;
            justify-content: space-between;
            column-gap: 15px;
        }
    }

    @media screen and (max-width: 590px) {
        &__contacts {
            flex-direction: row-reverse;
            margin-left: 0;
            max-width: none;

            &-direct {
                flex-direction: column;
            }
        }
    }

    &__container {
        max-width: 912px;
        justify-self: end;
        margin: 0;
        margin-left: auto;
        justify-content: flex-end;
    }

    &__divider {
        position: absolute;
        right: 0;
        width: 1010px;

        @media screen and (max-width: 1152px) {
            width: 736px;
        }

        @media screen and (max-width: 590px) {
            display: none;
        }
    }

    &__nav {
        margin-top: 20px;
    }

    &__division {
        display: none;

        @media screen and (max-width: 590px) {
            display: block;
            position: relative;

            .cross {
                transform: scale(0.75) translateX(-50%);
            }

            &::before {
                content: '';
                width: 200vw;
                background: white;
                height: 2px;
                position: absolute;
                left: -50px;
            }
        }
    }
}