.weekday {
    width: 322px;
    height: 54px;
    background: var(--gold);
    outline: solid 5px white;
    font-size: var(--contacts-fsz);
    color: rgba(17, 17, 17, 0.7);
    text-transform: capitalize;
    text-align: center;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 30px;
    right: 37px;
    top: -27px;

    @media screen and (max-width: 1152px) {
        width: 193px;
        height: 41px;
    }

    @media screen and (max-width: 540px) {
        width: 136px;
        height: 38px;
    }
}