.half-page {
    &__footer {
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        &-bottom {
            display: flex;
            justify-content: space-between;
            align-items: end;

            &--reverse {
                flex-direction: row-reverse;
            }
        }
    }

    &__angle {
        &--reverse {
            transform: scaleX(-1);


            @media screen and (max-width: 1152px) {
            }
        }

        
        @media screen and (max-width: 1152px) {
            transform: scale(calc(118 / 164)) translate(40px, 40px);
            
            &--reverse {
                transform: scaleX(calc(-118 / 164)) scaleY(calc(118 / 164)) translate(40px, 40px);

            }
        }
    }
}