// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: 'Roboto';
  // --content-width: 1170px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;

  --orange-brown-color: #6A5F40;
  --orange-brown-color-2: #754C17;
  --orange-brown-color-3: #95744A;
  --light-brown: #B9A489;
  --gold: #FFDA7B;
  --light-light-gray: #A1A1A1;

  --contacts-fsz: 22px;
  --description-fsz: 18px;
  --btn-fsz: 18px;
  --hero-title-fsz: 70px;
  --hero-description-fsz: 24px;
  --culture-title-fsz: 32px;
  --title-fsz: 50px;
  --large-fsz: 90px;
  --subtitle-fsz: 40px;
  --footer-title-fsz: 30px;
  --footer-description-fsz: 20px;

  --gap: 30px;

  @media screen and (max-width: 1152px) {
    --contacts-fsz: 16px;
    --description-fsz: 16px;
    --btn-fsz: 14px;
    --hero-title-fsz: 40px;
    --hero-description-fsz: 18px;
    --culture-title-fsz: 25px;
    --title-fsz: 30px;
    --large-fsz: 45px;
    --subtitle-fsz: 28px;
    --footer-title-fsz: 30px;
    --footer-description-fsz: 16px;
    --gap: 15px;
  }

  @media screen and (max-width: 340px) {
    --contacts-fsz: 12px;
    --description-fsz: 14px;
    --btn-fsz: 10px;
    --hero-title-fsz: 22px;
    --hero-description-fsz: 14px;
    --culture-title-fsz: 20px;
    --title-fsz: 28px;
    --large-fsz: 35px;
    --subtitle-fsz: 22px;
    --footer-title-fsz: 20px;
    --footer-description-fsz: 15px;
    --gap: 10px;
  }

}