.slider {
    &__swiper {

        &-pagination {
            display: flex;
            flex-direction: column;
            gap: 12px;
            width: 54px !important;

            .swiper-pagination-bullet-active {
                background: rgba(217, 217, 217, 0.7);
            }

            @media screen and (max-width: 580px) {
                display: none;
            }
        }

        &-bullet {
            font-size: 22px;
            cursor: pointer;
            width: 54px;
            height: 85px;
            color: #FFDA7B;
            font-family: "Hram";
            border-radius: 30px;
            display: flex;
            justify-content: center;
            align-items: center;
            border: solid 2px rgba(217, 217, 217, 0.7);


            @media screen and (max-width: 1152px) {
                width: 41px;
                height: 65px;
            }


        @media screen and (max-width: 1152px) {
            
        }
        }

        &-switch {
            stroke: #FFDA7B;
            z-index: 10;
            width: 122px;
            height: 60px;
            background: rgba(217, 217, 217, 0.75);
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 12px;

            &--dark {
                background: #9D9898;
            }

            &-btn {
                cursor: pointer;

                &:hover {
                    stroke: #2B2B2B;
                }

                &--next {
                    transform: scaleX(-1);
                }
            }

            &-divider {
                height: 23px;
                width: 1px;
                background: #FFDA7B;
            }


            @media screen and (max-width: 1152px) {
                transform: scale(calc(98 / 121)) translate(16px, 8px);
            }
        }
    }
}