.icons {
    padding: 100px 0 128px 50px;
    &__container {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: auto 1fr;
        gap: 28px;
    }

    &__swiper {
        width: calc(690 / 1440 * 100vw);
        overflow: hidden;
        position: relative;

        &-switch {
            position: absolute;
            bottom: 0;
            right: 0;
        }

        &-pagination {
            position: absolute;
            left: 12px !important;
            bottom: 65px !important;
            z-index: 20;
        }
    }

    &__slide {
        img {
            width: 100%;
            object-fit: fit;
        }
    }

    &__header {
    }
    &__center {
        grid-row: span 2;
    }
    &__slider-title {
        margin-bottom: 24px;
    }

    @media screen and (max-width: 1000px) {
        &__swiper-pagination {
            display: none;
        }
    }

    @media screen and (max-width: 768px) {
        padding: 80px 15px;
        &__container {
            grid-template-columns: 1fr;
        }

        
        &__swiper {
            width: calc(100vw - 60px);
            
        }

        &__header {
        }
        &__center {
            grid-row: span 2;
        }
        &__slider-title {
            margin-bottom: 24px;
        }  
    }
}