.contacts {
    display: flex;
    flex-direction: row;
    max-width: 620px;
    margin-left: auto;
    justify-content: space-between;
    align-items: center;

    &__social {
        display: flex;
    }

    &__direct {
        display: flex;
        gap: 12px;
        align-items: flex-end;

        &-info {
            font-size: var(--contacts-fsz);
        }
    }

    @media screen and (max-width: 1152px) {
        max-width: 448px;
    }
}