.burger {
    cursor: pointer;
    height: 20px;
    &__line {
        width: 26px;
        height: 2px;
        background: #000;
        transition: transform 0.3s;

        &--2 {
            transform: translateY(7px);
        }

        &--3 {
            transform: translateY(14px);
        }
    }

    &--active {
        position: fixed;
        top: 40px;
        right: 30px;
        z-index: 10;

        .burger__line {
            &--1 {
                transform: translateY(2px) rotate(45deg);
            }
            &--2 {
                transform: rotate(45deg);
            }
            &--3 {
                transform: translateY(-2px) rotate(-45deg);
            }
        }
    }
}