.news {
    padding: var(--hero-title-fsz) 0 56px;
    position: relative;
    z-index: 3;
    background: #F2F2F2;

    &__ornament {
        max-width: calc(365px / 1440px * 100vw) ;
        @media screen and (max-width: 768px) {
            max-width: 50%;
        }

        @media screen and (max-width: 340px) {
            max-width: 100%;
        }
    }

    &__header {
        margin-bottom: 40px;
        margin-left: calc(480 / 1440 * 100vw);

        @media screen and (max-width: 768px) {
            margin-left: 0;
        }
    }

    &__footer {
        margin-top: -56px;
    }

    &__center {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: var(--gap);
    }

    &__item {
        display: flex;
        flex-direction: column;
        justify-content: start;


        &:first-child {
            transform: translateY(-88px);
        }

        &:nth-child(2n) {
            flex-direction: column-reverse;
        }
        gap: 60px;
        &-img {
            position: relative;

            &-date {
                position: absolute;
                background: var(--gold);
                padding: 16px 40px;
                font-family: "Hram";
                color: rgba(17, 17, 17, 0.7);
                border-radius: 30px;
                border: solid 5px white;
                left: 32px;
                transform: translateY(-50%);

                @media screen and (max-width: 768px) {
                    width: 136px;
                    height: 42px;
                    padding: 0;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
            }

            img {
                width: 100%;
                height: 100%;
            }
        }

    }
    @media screen and (max-width: 768px) {

        &__center {
            grid-template-columns: 1fr;
            gap: 40px;
            margin-bottom: 40px;
        }

        &__item {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: var(--gap);

            &:first-child {
                transform: none;
            }

            &:nth-child(2n) {
                .news__item-img {
                    grid-column: 2;
                }

                .news__item-content {
                    grid-row: 1;
                }
            }
        }
    }


    @media screen and (max-width: 480px) {
        &__item {
            display: flex;
            flex-direction: column !important;
            gap: 30px;

        }
    }
}