.shrines {
    padding: 168px 0 88px;
    background: #F2F2F2;
    position: relative;

    @media screen and (max-width: 768px) {
        padding: 50px 0 50px;
    }

    @media screen and (max-width: 300px) {
        padding: 50px 0 50px;
    }

    &__top {
        position: absolute;
        top: -30px;
        left: 50%;
        transform: translateX(-50%);

        @media screen and (max-width: 768px) {
            // transform: scale(calc(226 / 445)) translate(-100%, -37px);
            width: 261px;
            height: 66px;
            top: -10px;
        }
    }

    &__header {
        grid-column: 2;
    }
    &__center {
        grid-row-start: 1;
        grid-row-end: 3;
    }
    &__footer {
        grid-column: 2;
        grid-row: 2;
    }

    @media screen and (max-width: 768px) {
        &__header {
            grid-row: auto;
            grid-column: auto
        }
        &__center {
            grid-row: auto;
            grid-column: auto;
        }
        &__footer {
            grid-row: auto;
            grid-column: auto;
        }
    }

    &__video-name {
        margin-bottom: 24px;
    }
    &__container {
        gap: 28px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: auto 1fr;
        @media screen and (max-width: 768px) {
            grid-template-columns: 1fr;
        }
    }

}