.btn {
    background-color: rgba(255, 255, 255, 0);
    outline: none;
    border: solid 1px white;
    color: white;
    font-size: var(--btn-fsz);
    font-family: "Hram";
    text-transform: uppercase;
    padding: 16px 24px;
    transition: background-color 0.3s;
    display: flex;
    align-items: center;
    justify-content: center;

    @media screen and (max-width: 768px) {

        padding: 8px 12px;
    }

    &:hover {
        background-color: rgba(255, 255, 255, 0.3);

        .btn__arrow {
            transform: translateX(8px);
        }
    }

    &__arrow {
        position: relative;
        transition: transform 0.3s;
        width: 32px;
        margin-left: 8px;

        @media screen and (max-width: 768px) {

            width: 16px;
        }

        &-line {
            position: absolute;
            height: 2px;
            border-top-left-radius: 2px;
            border-bottom-left-radius: 2px;

            &--1 {
                width: 8px;
                transform: rotate(45deg);
                right: 0;
                bottom: 1.2px;
            }

            &--2 {

                border-top-right-radius: 3px;
                border-bottom-right-radius: 3px;
                width: 32px;
            }

            &--3 {
                width: 8px;
                transform: rotate(-45deg);
                right: 0;
                top: 3px;
            }

            @media screen and (max-width: 768px) {
                &--1 {
                    width: 4px;
                    bottom: 0;
                }

                &--2 {
                    width: 16px;
                }

                &--3 {
                    width: 4px;
                    top: 2px;
                }
            }

            background: white;
        }
    }

    &--brown {
        border-color: var(--orange-brown-color-3);
        color: var(--orange-brown-color-3);

        .btn__arrow {
            &-line {
                background: var(--orange-brown-color-3);
            }
        }
    }

    &--filed-brown {
        border-color: var(--orange-brown-color-3);
        background: var(--orange-brown-color-3);

        &:hover {
            background: var(--orange-brown-color-3);
        }

    }

    &--wide {
        padding-left: 50px;
        padding-right: 50px;

        @media screen and (max-width: 768px) {
            padding-left: 25px;
            padding-right: 25px;
        }
        @media screen and (max-width: 540px) {
            padding-left: 10px;
            padding-right: 10px;
        }
    }

    &-play {
        position: relative;

        &__key {
            cursor: pointer;
            border-radius: 50%;
            overflow: hidden;
        }


        &__pulse {
            width: 200px;
            height: 200px;
            border-radius: 50%;
            background: #FFCB12;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: -1;
            animation: play-pulse 2s ease infinite;
        }

        &__frame {
            z-index: -1;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
}

@keyframes play-pulse {
    0% {
        transform: translate(-50%, -50%) scale(0);
        opacity: 1;
    }

    100% {
        transform: translate(-50%, -50%) scale(1);
        opacity: 0;
    }
}