.culture {
    margin-top: 28px;

    &__container {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 32px;

        @media screen and (max-width: 690px) {
            grid-template-columns: 1fr;
            gap: var(--gap);
        }
    }

    @media screen and (max-width: 690px) {
        &__frame {
            transform: scaleX(-1);
        }
    }

    &__content {
        padding: 48px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;

        &--left {
            padding-right: 118px;
        }

        &--center {
            padding-left: 104px;
            padding-right: 104px;

            
        }


        @media screen and (max-width: 1152px) {
            padding: 40px;
        }

        

        &-bottom {
            margin-top: 60px;

            @media screen and (max-width: 1152px) {
                margin-top: 24px;
            }

            display: flex;
            width: 100%;

            &--left {
                justify-content: end;
            }

            &--center {
                justify-content: start;

                @media screen and (max-width: 690px) {
                    justify-content: end;

                    
                }
            }
        }
    }

    &__item {
        min-height: 328px;
        position: relative;
        color: white;

        @media screen and (max-width: 690px) {
            min-height: auto;
        }

        &-title {
            margin: 0;
            font-family: "Hram";
            text-transform: uppercase;
            font-size: var(--culture-title-fsz);
        }

        &-description {
            font-size: var(--description-fsz);
        }

        &-img {
            position: absolute;
            object-fit: fit;
            z-index: -1;
            // object-position: top;
            width: 100%;
            height: 100%;
        }
    }
}