.hero {
    &__slide {
        height: 100vh;
        position: relative;

        &-container {
            height: 100%;
            width: 653px;
            display: flex;
            // align-items: center;
            justify-content: center;
            margin-left: auto;
            margin-right: 60px;
            display: flex;
            flex-direction: column;

            @media screen and (max-width: 1152px) {
                max-width: 340px;
            }
            @media screen and (max-width: 590px) {
                max-width: 340px;
                display: flex;
                text-align: center;
                justify-content: center;
                align-items: center;
                margin: 0 auto;
            }
            @media screen and (max-width: 340px) {
                max-width: 240px;
                display: flex;
                text-align: center;
                justify-content: center;
                align-items: center;
            }
        }

        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: -1;
        }

        &-title {
            color: white;
            font-size: var(--hero-title-fsz);
            font-family: "Hram";
        }

        &-description {
            color: white;
            font-size: var(--hero-description-fsz);
        }
    }

    &__swiper {
        position: relative;
        &-pagination {
            position: absolute;
            left: 15px !important;
            bottom: 74px !important;
            z-index: 20;

            
        }

        &-switch {
            right: 66px;
            bottom: 72px;
            position: absolute;

            @media screen and (max-width: 340px) {
                right: 0;
                bottom: 0;
            }
        }

        @media screen and (max-width: 340px) {
            width: 100vw;
        }


        
    }
}