.ornament {
    display: flex;
    align-items: center;
    width: 100%;
    &__basis {
        width: 100%;
        height: 1px;
        background: var(--light-light-gray);
    }

    &--right {
        transform: scaleX(-1);
    }

    @media screen and (max-width: 768px) {
        max-width: 464px;
    }

    @media screen and (max-width: 340px) {
        &--left {
            display: none;
        }
    }

    &--require {
        display: flex;
    }
}