.play {
    // overflow: visible;
    position: relative;
    &__wrapper {
        z-index: 10;
        height: 226px;
        width: 226px;
        background: white;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translateX(-50%) translateY(-50%);

    }


    @media screen and (max-width: 1152px) {
        transform: scale(calc(136 / 226));
        z-index: 2;
    }

    @media screen and (max-width: 340px) {
        transform: scale(calc(90 / 226));
        z-index: 2;
    }
}