.frame {
    pointer-events: none;
    border: 2px solid var(--gold);
    width: calc(100% - 18px * 2);
    height: calc(100% - 16px * 2);
    position: absolute;
    left: var(--description-fsz);
    top: 16px;

    &--mobile {
        display: none;

        @media screen and (max-width: 590px) {
            display: block;
        }
    }

    &--smooth {
        width: calc(100% - 16px * 2);
        height: calc(100% - 16px * 2);
    }

    &__angle {
        position: relative;
        width: 100%;
        height: 100%;

        &-img {
            position: absolute;


            @media screen and (max-width: 1152px) {
                transform: scale(0.5) translate(-56px, 40px);
            }

            &--culture-left {
                bottom: 19px;
                left: 30px;

            }

            &--culture-right {
                transform: scaleX(-1);
                bottom: 19px;
                right: 22px;
                @media screen and (max-width: 1152px) {
                    transform: scale(-0.5, 0.5) translate(-56px, 40px);
                }

                &-top {
                    transform: scale(-1, -1);
                    right: 7px;
                    top: 7px;

                @media screen and (max-width: 1152px) {
                    transform: scale(-0.5, -0.5) translate(-56px, 40px);
                }
                }
            }

        }
    }
}