.about {
    padding: 65px 0 40px;
    counter-reset: slides;

    @media screen and (max-width: 768px) { 
        padding: 35px 0 0;
    }

    &__swiper {
        display: grid;
        grid-template-columns: repeat(2, 1fr);

        @media screen and (max-width: 1152px) {
            grid-template-columns: 1fr;
        }

        gap: var(--gap);
        position: relative;

        &-switch {
            position: absolute;
            bottom: 0;
            right: 0;
            z-index: 10000;
        }

        &-header {
            

            &-top {
                height: 100%;
                position: relative;
            }


            @media screen and (max-width: 1152px) {
                grid-column: span 2;
            }

        }

        &-center {
            grid-column: span 2;
        }

        &-bottom {
            grid-row: 1;
            grid-column: 2;
            font-size: var(--description-fsz);

            @media screen and (max-width: 1152px) {
                grid-row: 3;
                grid-column: span 2;
            }

            // @media screen and (max-width: 440px) {
            //     margin-top: 20px;
            // }

            @media screen and (max-width: 1152px) {
                margin-top: 20px;
            }
        }

        &-title {
            &-top {
                font-family: "Hram";
                color: #754C17;
                font-size: var(--large-fsz);
                display: flex;
                align-items: center;
                gap: 20px;
            }

            &-bottom {
                font-family: "Hram";
                color: #B9A489;
                font-size: var(--subtitle-fsz);
                @media screen and (max-width: 440px) {
                    width: 220px;
                }
            }

            &-dot {
                width: 19px;
                height: 19px;
                background: var(--gold);
                border-radius: 50%;
            }

            
        }
    }

    &__container {
        margin-bottom: 45px;
    }

    &__slide {
        width: 100%;
        height: 100%;
        position: relative;

        img {
            width: 100%;
            height: 100%;
            object-fit: fit;
        }


    }

    &__slide-after {
        &::after {
            counter-increment: slides;
            content: (counter(slides));
            display: block;
            height: 54px;
            width: 85px;


            @media screen and (max-width: 1152px) {
                height: 31px;
                width: 55px;
            }

            position: absolute;
            background: var(--gold);
            display: flex;
            justify-content: center;
            align-items: center;
            border: solid 5px white;
            border-radius: 30px;
            left: 35px;
            bottom: 0;
            transform: translateY(50%);
            font-family: "Hram";
            font-size: 22px;
            color: rgba(17, 17, 17, 0.7);
        }

        &:nth-of-type(-n + 9)::after {
            content: ("0" counter(slides));
        }
    }

    &__header {
        margin-bottom: 45px;
    }
}