.footer {
    padding: 80px 0 50px;
    background-image: url("../img/footer-back.jpg");
    background-size: cover;
    background-repeat: no-repeat;

    &__container {
        padding-left: 65px;
        padding-right: 65px;

        @media screen and (max-width: 570px) {
            padding-left: 10px;
            padding-right: 10px;
        }
    }

    &__info {
        display: grid;
        grid-template-columns: 1fr 2fr;

        @media screen and (max-width: 768px) {
            display: flex;
            flex-direction: column;
            gap: 40px;

            &-left {
                max-width: 360px;
                margin: 0 auto;
            }
        }

        &-right {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 100px;

            @media screen and (max-width: 570px) {
                gap: 20px;
            }
        }
    }

    &__contacts {
        margin: 0 auto;
    }

    &__column {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        gap: 23px;

        &-header {
            width: 100%;
        }

        &-title {
            color: var(--gold);
            font-size: var(--footer-title-fsz);
            font-family: "Hram";
        }

        &-division {
            display: flex;
            gap: 12px;

            &-dot {
                width: 7px;
                height: 7px;
                border-radius: 50%;
                background: #E3CC91;
            }
        }

        &-description {
            color: #D2D2D2;
            font-size: var(--description-fsz);
        }

        &--center {
            // justify-content: end;
            text-align: end;
            align-items: end;
        }

        &--right {
            text-align: start;
            align-items: start;
        }

    }

    &__copyright {
        margin: 0 32px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__contacts {
        margin: 0;
        padding: 0;
        list-style-type: none;
        display: flex;
        flex-direction: column;
        gap: 8px;

        &-item {
            font-size: var(--footer-description-fsz);
            color: #C2C1C1;
        }

        &-social {
            gap: 16px;
        }
    }

    &__nav {
        &-ul {
            display: flex;
            flex-direction: column;
            gap: 8px;
        }

        &-item {
            a {
                color: #C2C1C1;
                font-size: var(--footer-description-fsz);
            }
        }
    }
}