.nav {
    display: flex;

    


    &__ul {
        display: flex;
        list-style-type: none;
        font-family: "Hram";
        justify-content: space-between;
        width: 100%;
        margin-left: auto;
        text-transform: uppercase;

        @media screen and (max-width: 1152px) {
            max-width: 640px;
        }
    }

    &__link {
        color: black;
        font-size: 18px;
        transition: color 0.3s;

        &:hover {
            color: var(--orange-brown-color-2);
        }

        @media screen and (max-width: 1152px) {
            font-size: 12px;
        }
    }

    &__item {
        position: relative;
        &-after {
            pointer-events: none;
            display: flex;
            opacity: 0;
            width: 100%;
            left: 50%;
            transform: translateX(-50%);
            position: absolute;
            justify-content: center;
            align-items: center;
            bottom: -10px;
            gap: 4px;
            transition: opacity 0.3s;
            &-line {
                transition: max-width 0.5s;
                border-radius: 4px;
                height: 2px;
                width: 100%;
                max-width: 0;
                background: var(--orange-brown-color-2);
            }

            &-dot {
                width: 4px;
                height: 4px;
                background: var(--orange-brown-color-2);
                border-radius: 50%;
            }
        }

        &:hover {
            .nav__item-after {
                display: flex;
                opacity: 1;

                &-line {
                    max-width: 25%;

                }
            }
        }
    }

    @media screen and (max-width: 590px) {
        background: white;
        position: fixed;
        top: 0;
        left: 0;
        margin: 0;
        padding: 0;
        width: 100vw;
        height: 338px;
        display: none;

        &--active {
            display: block;
        }

        &__frame {
            bottom: 0;
        }

        &__wrapper {
            position: absolute;
            left: 0;
            right: 0;
            height: 100%;
            width: 100%;
        }

        &__ul {
            flex-direction: column;
            justify-content: start;
            gap: 10px;
            margin: 0;
            padding: 0;
            padding-top: 53px;
            padding-left: 80px;
        }

        &__item {
            position: relative;
            &::before {
                width: 9px;
                height: 9px;
                content: '';
                display: block;
                background: var(--gold);
                border-radius: 50%;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: -21px;
            }

            &-after {
                display: none !important;
            }
        }
    }
}