.videos {
    background: #F2F2F2;
    padding: 60px 0 160px;
    position: relative;
    overflow: hidden;
    z-index: 1;

    @media screen and (max-width: 768px) {
        padding: 25px 0 75px;
    }

    // @media screen and (max-width: 300px) {
    //     padding: 50px 0 50px;
    // }

    &__header {
        margin-bottom: 43px;
    }

    &__center{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: var(--gap);
        margin-bottom: 50px;

        @media screen and (max-width: 440px) {
            grid-template-columns: 1fr;
        }
    }

    &__video {
        img {
            width: 100%;
            height: 100%;
        }
    }

    &__back {
        position: absolute;
        z-index: -1;

        &--1 {
            bottom: 0;
            left: 88px;
        }

        &--2 {
            bottom: 0;
            right: 88px;
        }

        @media screen and (max-width: 900px) {
            &--1 {
                height: 984px;
                width: 97px;
                bottom: 40px;
            }
    
            &--2 {
                height: 984px;
                width: 97px;
                bottom: 40px;
            }
        }

        @media screen and (max-width: 900px) {
            &--1 {
                left: -50px;
            }
    
            &--2 {
                right: -50px;
            }
        }
    }
}