.social {
    &-media {
        display: flex;
        width: 36px;
        height: 36px;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        transition: border 0.3s;
        border: solid 1px rgba(0, 0, 0, 0);

        &:hover {
            border: solid 1px var(--orange-brown-color);
        }

        &--light {
            &:hover {
                border: solid 1px #D3BA7B;
            }
        }
    }
}