.schedule {
    padding-top: 80px;
    padding-bottom: 85px;
    position: relative;
    z-index: 2;

    @media screen and (max-width: 768px) {
        padding: 25px 0 40px;
    }

    &__background {
        position: absolute;
        z-index: -1;
        bottom: -325px;
        right: 0;
    }

    &__header {
        margin-bottom: 25px;
    }

    &__ul {
        list-style-type: none;
        font-family: "Hram";
        padding: 0;

    }
    &__li {
        position: relative;
        background: rgba(217, 217, 217, 0.1);
        border: solid 1px #C6C6C6;
        padding: 21px 62px 28px;
        border-radius: 20px;
        display: flex;
        gap: 40px;
        margin-bottom: 43px;
    }

    &__day {
        font-size: 64px;
    }

    &__month {
        font-size: 28px;
    }

    &__year {
        font-size: 28px;
    }

    @media screen and (max-width: 768px) {
        &__day {
            font-size: 32px;
        }
    
        &__month {
            font-size: 18px;
        }
    
        &__year {
            font-size: 14px;
        }

        &__cross {
            transform: scale(calc(34 / 52))
        }
    }

    

    &__correctly {
        font-size: var(--subtitle-fsz);
        color: var(--light-brown);
        margin-top: 24px;

        @media screen and (max-width: 1152px) {
            margin-top: 8px;
        }
    }

    &__time {
        display: flex;
        width: 100%;
        justify-content: space-between;
        max-width: 472px;

        @media screen and (max-width: 1152px) {
            flex-direction: column;
            justify-content: start;
        }
    }

    &__content {
        // margin-top: 28px;
        font-size: var(--description-fsz);
        display: flex;
        align-items: center;
        font-family: 'Roboto';
        // max-width: 688px;
    }

    &__back {
        position: absolute;
        top: -116px;
        left: 50%;
        transform: translateX(-50%);

        @media screen and (max-width: 768px) {
            // transform: scale(calc(226 / 445)) translate(-100%, -37px);
            width: 261px;
            height: 66px;
            top: -65px;
        }
    }

    @media screen and (max-width: 768px) {
        &__li {
            padding: 30px;
        }
    }


    @media screen and (max-width: 530px) {

        &__time {
            flex-direction: row;
            justify-content: space-between;
        }
        &__day {
            font-size: 32px;
        }
    
        &__month {
            font-size: 18px;
        }
    
        &__year {
            font-size: 14px;
        }

        &__cross {
            transform: scale(calc(34 / 52))
        }

        &__li {
            flex-direction: column;
            gap: 10px;
            padding: 20px;
        }
    }
}